const appleIphone = /iPhone/i;
const appleIpod = /iPod/i;
const appleTablet = /iPad/i;
const appleUniversal = /\biOS-universal(?:.+)Mac\b/i;
const androidPhone = /\bAndroid(?:.+)Mobile\b/i; // Match 'Android' AND 'Mobile'
const androidTablet = /Android/i;
const windowsPhone = /Windows Phone/i;
const windowsTablet = /\bWindows(?:.+)ARM\b/i; // Match 'Windows' AND 'ARM'
const otherBlackBerry = /BlackBerry/i;
const otherBlackBerry10 = /BB10/i;
const otherOpera = /Opera Mini/i;
const otherChrome = /\b(CriOS|Chrome)(?:.+)Mobile/i;
const otherFirefox = /Mobile(?:.+)Firefox\b/i; // Match 'Mobile' AND 'Firefox'

const match = (regex: RegExp): boolean => regex.test(navigator.userAgent);

export const isMobile =
  match(appleIphone) ||
  match(appleIpod) ||
  match(appleTablet) ||
  match(appleUniversal) ||
  match(androidPhone) ||
  match(androidTablet) ||
  match(windowsPhone) ||
  match(windowsTablet) ||
  match(otherBlackBerry) ||
  match(otherBlackBerry10) ||
  match(otherOpera) ||
  match(otherFirefox) ||
  match(otherChrome);

export const isIOS = 
  match(appleIphone) ||
  match(appleIpod) ||
  match(appleTablet);